$arrow-size: 135px;

.whatDoWeDo {
    .card {
        margin: 1rem;
    }

    .mission-vision-container {
        display: flex;
        padding: 1rem 0;

        & > * {
            flex: 1;
        }

        .mission-vision-container__card {
            text-align: center;

            h1 {
                color: var(--color-primary);
            }
        }

        .mission-vision-container__arrow-container {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;
            .arrow-container__arrow-left {
                width: $arrow-size;
                height: auto;
                transform: rotate(210deg);
            }
            .arrow-container__arrow-right {
                width: $arrow-size;
                height: auto;
                transform: rotate(30deg);
            }
        }
    }

    .inspiration-container {
        margin: 1rem 15vw;
        text-align: center;

        h1 {
            text-transform: uppercase;
        }

        .card__link {
            color: var(--color-text);
            cursor: pointer;
        }
    }

    .history-container {
        margin-top: 2rem;
        h1 {
            text-transform: uppercase;
            text-align: center;
            color: var(--color-text);
        }

        .image-container {
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 20vw;
                height: auto;
            }
        }
    }
}
